.Icon {
    width: 40vw;
    vertical-align: center;
    text-align: center;
}

#icon {
    border-radius: 50%;
    width: .9cm;
    height: .9cm;
    -webkit-filter: 50%;
            filter: 50%;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    -webkit-filter: invert(100%);
    /* filter: invert(100%); */
    opacity: 0.2;
}

#icon:hover {
    cursor: pointer;
    opacity: 0.7;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1)
}


