.Project {
    width: 40vw;
    vertical-align: top;
    text-align: center;
    margin-bottom: 5vh;
}

.ProjectBackground {
    height: 45vh;
    width: 100%;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
    background-size: 100%;
}

#desc {
    font-size: 2vh;
    color: rgb(120, 120, 120);
    -webkit-font-smoothing: antialiased;
    line-height: 3vh;
    font-weight: lighter;
    text-align: justify;

}


#title {
    font-size: 2.5vh;
    -webkit-font-smoothing: antialiased;
    font-weight: bold;
    color: rgb(15, 15, 15);   
}

#image {
    -o-object-fit: cover;
       object-fit: cover;
    width: 40vw;
    height: 45vh;
    border-radius: 0.3vw;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

#image:hover {
    cursor: pointer;
    height: 55vh;
}


